h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

*, :before, :after {
  box-sizing: border-box;
}

a, a:link, a:visited {
  text-decoration: none;
}

:root {
  --primary-text-color: #f8f8f8;
  --secondary-text-color: #b7b7b7;
  --primary-title-color: #fff;
  --primary-bgc: #282828;
  --primary-btn-color: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --primary-ligth-theme-text-color: #282828;
  --secondary-ligth-theme-text-color: #595959;
  --ligth-theme-title-color: #111;
  --ligth-theme-bgs: #fff;
  --primary-ligth-theme-btn-color: linear-gradient( 141.22deg, #ffc226 9.4%, #f84119 91.91% );
  --accent-color: #f87719;
  --black: #000;
  --white: #fff;
}

body {
  color: var(--primary-text-color);
  background-color: var(--primary-bgc);
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

.container {
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

.hero-gradient {
  background: linear-gradient(86.77deg, #111 30.38%, rgba(17, 17, 17, 0) 65.61%);
}

.hero-img {
  background-image: url("hero-home-mob1.9476a76e.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.hero-title {
  color: #fff;
  margin-right: 101px;
  padding-top: 40px;
  padding-bottom: 8px;
  padding-left: 20px;
  font-family: Roboto;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.18;
}

.hero-description {
  color: #f8f8f8;
  margin-right: 134px;
  padding-bottom: 32px;
  padding-left: 20px;
  font-family: Roboto;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.14;
  display: flex;
}

.hero-btn {
  width: 110px;
  height: 38px;
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border-radius: 74px;
  margin-bottom: 80px;
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
}

.hero-btn:hover, .hero-btn:focus {
  background: #fff;
  border: 2px solid #f3c653;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.backdrop {
  z-index: 20;
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: rgba(46, 47, 66, .4);
  transition: opacity .5s cubic-bezier(.075, .82, .165, 1), visibility .5s cubic-bezier(.075, .82, .165, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(.8);
}

.modal {
  z-index: 30;
  width: 704px;
  min-height: 488px;
  background: #111;
  border-radius: 16px;
  padding: 44px;
  transition: transform .5s cubic-bezier(.075, .82, .165, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 1px 1px 14px 4px rgba(255, 107, 8, .42);
}

.close-btn {
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
  transition: border-color .5s cubic-bezier(.075, .82, .165, 1), background-color .5s cubic-bezier(.075, .82, .165, 1);
  position: absolute;
  top: 30.75px;
  right: 30.75px;
}

.weekly-title-wrap {
  justify-content: space-between;
  margin-top: 40px;
  padding-bottom: 24px;
  font-size: 16px;
  font-weight: 500;
  line-height: 19px;
  display: flex;
}

.weekly-title-text {
  text-transform: uppercase;
  color: var(--white);
}

.weekly-title-link {
  color: var(--accent-color);
}

.weekly-card {
  background: linear-gradient(rgba(0, 0, 0, 0) 63.48%, rgba(0, 0, 0, .9) 92.16%);
  border-radius: 5px;
  margin-bottom: 16px;
  position: relative;
}

.weekly-container-image {
  position: relative;
}

.overlay {
  width: 100%;
  height: 300px;
  background: linear-gradient(rgba(0, 0, 0, 0) 63.48%, rgba(0, 0, 0, .95) 92.16%);
  position: absolute;
  bottom: 0;
  left: 0;
}

.weekly-card-image {
  width: 100%;
  border-radius: 5px;
