  left: 12px;
}

.weekly-card-description-title {
  display: block;
}

.weekly-card-raiting {
  position: absolute;
  bottom: 12px;
  right: 12px;
}

.upcoming_wrap {
  font-size: 16px;
  font-weight: 500;
  line-height: 19px;
}

.upcoming-title {
  text-transform: uppercase;
  margin-bottom: 24px;
  font-size: 16px;
}

.upcoming-card-img {
  width: 805px;
  height: 458px;
}

.upcoming-card-img img {
  border-radius: 5px;
}

.upcoming-card-title {
  color: var(--white);
  margin-top: 24px;
  margin-bottom: 20px;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 23px;
}

.upcoming-card-release-wrap {
  margin-bottom: 12px;
}

.upcoming-card-release-text {
  color: var(--secondary-text-color);
  margin-right: 57px;
}

.upcoming-card-release-data {
  color: var(--accent-color);
}

.upcoming-card-vote-wrap {
  margin-bottom: 12px;
  display: flex;
}

.upcoming-card-vote {
  color: var(--secondary-text-color);
  margin-right: 71px;
}

.upcoming-card-vote-data {
  gap: 4px;
  display: flex;
}

.upcoming-card-vote-data :first-child, .upcoming-card-vote-data :last-child {
  color: var(--black);
  background: var(--white);
  border-radius: 5px;
  padding: 1px 8px;
}

.upcoming-card-popularity-wrap {
  margin-bottom: 12px;
}

.upcoming-card-popularity-text {
  color: var(--secondary-text-color);
  margin-right: 79px;
}

.upcoming-card-popularity-data {
  color: var(--white);
}

.upcoming-card-genre-wrap {
  margin-bottom: 12px;
}

.upcoming-card-genre-text {
  color: var(--secondary-text-color);
  margin-right: 103px;
}

.upcoming-card-genre-data {
  color: var(--white);
}

.upcoming-card-about-title {
  text-transform: uppercase;
  color: var(--secondary-text-color);
  margin-bottom: 20px;
  display: block;
}

.upcoming-card-about-text {
  color: var(--primary-text-color);
  margin-bottom: 20px;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
}

.upcoming-button {
  cursor: pointer;
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border-radius: 74px;
  margin-bottom: 40px;
  padding: 12px 24px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
}

@media screen and (min-width: 768px) {
  .weekly-title-wrap {
    margin-top: 52px;
    font-size: 24px;
    line-height: 28px;
  }

  .weekly-card-description {
    line-height: 18px;
  }

  .weekly-cards-wrap {
    gap: 16px;
    margin-bottom: 44px;
    display: flex;
  }

  .upcoming-title {
    line-height: 28px;
  }

