    left: 20px;
  }

  .weekly-cards-wrap {
    margin-bottom: 60px;
  }

  .weekly-container-image {
    width: 395px;
  }

  .upcoming-title {
    margin-bottom: 28px;
    font-size: 28px;
    line-height: 33px;
  }

