.search-form {
  justify-content: center;
  align-items: center;
  gap: 16px;
  display: flex;
}

.search-form-input {
  width: 173px;
  height: 40px;
  color: #595959;
  border: 1px solid #595959;
  border-radius: 8px;
  padding: 12px 20px;
  font-size: 14px;
  font-weight: 500;
  line-height: 16px;
}

.search-btn {
  width: 40px;
  height: 40px;
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border: 0;
  border-radius: 50%;
  padding: 11px;
}

.search-btn:hover {
  border: 1px solid linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  background: #fff;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.search-icon {
  width: 100%;
  height: 100%;
  fill: currentColor;
}

.search-btn:hover .search-icon {
  fill: #f87719;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.catalog-films {
  display: flex;
}

.catalog-info-border {
  border-left: 1px solid #fff;
}



:root {
  --star-size: 60px;
  --star-color: rgba(248, 159, 25, .68);
  --star-background: #f84119;
}

.Stars {
  --percent: calc(var(--rating) / 5 * 100%);
  font-size: var(--star-size);
  font-family: Times;
  line-height: 1;
  display: inline-block;
}

.Stars:before {
  content: "★★★★★";
  letter-spacing: 3px;
  background: linear-gradient( 90deg, var(--star-background) var(--percent), var(--star-color) var(--percent) );
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}

.is-trailer-hidden {
  opacity: 0;
  visibility: hidden;
  display: none;
}

.is-scroll-block {
  overflow: hidden;
}

.hero-trailer-backdrop {
  width: 100vw;
  height: 100vh;
  opacity: 1;
  z-index: 1;
  background-color: rgba(24, 23, 23, .77);
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.hero-trailer-backdrop.is-trailer-hidden {
  transition: opacity 1s linear, visibility .3s linear;
}

.trailer-iframe {
  width: 100%;
  height: 100%;
}

.trailer-container {
  text-align: center;
  background-color: rgba(36, 35, 35, .17);
  border-radius: 20px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  overflow: hidden;
  box-shadow: 0 0 60px #f87719;
}

@media screen and (min-width: 320px) {
  .trailer-container {
    width: 280px;
    border-radius: 30px;
    flex-direction: column;
  }
}

@media screen and (min-width: 768px) {
  .trailer-container {
    width: 650px;
    height: 360px;
    border-radius: 40px;
    flex-direction: row;
    align-items: center;
  }
}

@media screen and (min-width: 1280px) {
  .trailer-container {
    width: 1080px;
    height: 600px;
    border-radius: 50px;
  }
}



